import {
  SourceSelectionInitialStateFullV2,
  SourceSelectorSelectedCategoryInfo,
  SourceSelectorSelectedSourceInfoV2,
} from "../sourceSelector.types"

type ResetSourceSelectorStateProps = {
  initialStateFull: SourceSelectionInitialStateFullV2 | null
  setSelectedSources: (sources: Array<SourceSelectorSelectedSourceInfoV2>) => void
  setSelectedCategories: (categories: Array<SourceSelectorSelectedCategoryInfo>) => void
  setIsFilterInclude: (isFilterInclude: boolean) => void
}

function resetSourceSelectorStateV2({
  setSelectedSources,
  setSelectedCategories,
  setIsFilterInclude,
  initialStateFull,
}: ResetSourceSelectorStateProps): void {
  setSelectedSources(initialStateFull?.sources || [])
  setSelectedCategories(initialStateFull?.categories || [])
  setIsFilterInclude(initialStateFull?.isFilterInclude ?? true)
}

export { resetSourceSelectorStateV2 }
